/**
 * Pagination
 */
@import 'pagination-variabes';
@import '../../shared/styles/pagination';

.ch-pagination {
    font-size: $pagination-text-size;
}

.ch-pagination .ch-pagination-sequence a {
    background-color: $pagination-next-and-prev-bg-color;
    border-color: $pagination-next-and-prev-border;
}
