/**
 * Reset
 * @based on: Eric Meyer
 */
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, address, big, cite, code,
del, dfn, em, img, ins, q, s,
small, strong, sub, sup,
b, u, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
abbr, article, aside, audio, canvas,
datalist, details, summary, dialog, figcaption, figure,
footer, header, main, mark, menu, meter, nav, output,
progress, section, time, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
}

ol, ul, menu {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

/* remember to highlight inserts somehow!*/
ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup*/
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/**
 * Base
 */
/*
 * 1. Sets default font family to sans-serif.
 * 2. Prevents iOS text size adjust after orientation change, without disabling
 *    user zoom.
 * 3. Prevents the overlap of :active state by tap-highlight-color that is
 *    appears with delay of 300ms
 */
html {
  font-family: sans-serif;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-tap-highlight-color: transparent;
  /* 3 */
}

body {
  font-family: Arial, Helvetica, "Nimbus Sans L", sans-serif;
  font-size: 14px;
  line-height: 21px;
  padding: 0 10px;
  /* Prevent layout "sticking" to the edge of the browser */
  background-color: #fff;
  color: #333;
  margin: 0 auto;
}

/**
 * Misc
 */
hr {
  border: none;
  border-bottom: 1px dashed #ccc;
}

a img {
  border: none;
}

p + p {
  margin: 1em 0 0 0;
}

img {
  max-width: 100%;
}

/**
 * Icons
