/**
 * List boxes
 */
.ch-box-list {
  background-color: #f0f0f0;
  border: 1px solid #dedede;
  padding: 0;
}

.ch-box-list li {
  position: relative;
  border-bottom: 1px dotted #cdcdcd;
  color: #666;
  font-size: 16px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
}

.ch-box-list li:last-child {
  border-bottom: none;
}

.ch-box-list li:active {
  background-color: #e9e9e9;
}

/**
 * List boxes: anchors within
 */
.ch-box-list li a {
  line-height: 25px;
  text-decoration: none;
  display: block;
  padding: 14px 42px 14px 20px;
  position: relative;
}

.ch-box-list li a:after {
  content: '\f054';
  font-size: 19px;
  position: absolute;
  right: 20px;
  top: 50%;
  margin-top: -11px;
  color: #999;
}

/**
 * List boxes: icons within
 */
.ch-box-list [class*='ch-icon'] {
  position: absolute;
  top: .75em;
  left: 16px;
  color: #999;
}

.ch-box-list [class*='ch-icon'] + a {
  padding-left: 48px;
}

/**
 * Buttons
 */
/**
 * Buttons
 */
/* Global */
[class*="ch-btn"],
[class*="ch-btn"]:focus {
  font-family: Arial, Helvetica, "Nimbus Sans L", sans-serif;
  border-radius: 4px;
  box-shadow: none;
  cursor: pointer;
  display: inline-block;
  height: auto;
  text-decoration: none;
  font-size: 18px;
}

/* Focus */
[class*="ch-btn"]:focus,
input[type="button"]:focus,
input[type="submit"]:focus,
input[type="reset"]:focus,
input[type="file"]::-webkit-file-upload-button:focus,
button:focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

/* Colors */
.ch-btn,
.ch-btn:focus,
.ch-btn:visited,
a.ch-btn {
  background-color: #3e52a1;
  background-image: -webkit-linear-gradient(#3e52a1, #2d3277);
  background-image: linear-gradient(#3e52a1, #2d3277);
  box-shadow: 0 1px 0 #7a89e8 inset;
  border: 1px solid #2d3277;
  color: #fff;
}

.ch-btn-skin,
.ch-btn-skin:focus,
.ch-btn-skin:visited,
a.ch-btn-skin {
  background-color: #cce3f7;
  background-image: -webkit-linear-gradient(#cce3f7, #aaD0ef);
  background-image: linear-gradient(#cce3f7, #aaD0ef);
  border: 1px solid #79a8c7;
  box-shadow: 0 1px 0 #e5f2fc inset;
  color: #476274;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.5);
}

/* Hover */
.ch-btn:hover,
.ch-btn-skin:hover {
  text-decoration: none;
}

/* Colors on Hover */
.ch-btn:hover {
  background-color: #4355d2;
  background-image: -webkit-linear-gradient(#4355d2, #2f33ae);
  background-image: linear-gradient(#4355d2, #2f33ae);
  box-shadow: 0 1px 0 #7a89e8 inset;
}

.ch-btn-skin:hover {
  background-color: #dcecfb;
  background-image: -webkit-linear-gradient(#dcecfb, #c5dff5);
  background-image: linear-gradient(#dcecfb, #c5dff5);
  box-shadow: 0 1px 0 #e5f2fc inset;
}

/* Colors on Active */
.ch-btn:active {
  background-color: #292f66;
  background-image: -webkit-linear-gradient(#292f66, #202388);
  background-image: linear-gradient(#292f66, #202388);
  box-shadow: 0 1px 6px #141452 inset;
  border-color: #181e39;
}

.ch-btn-skin:active {
  background-color: #a2c9e1;
  background-image: -webkit-linear-gradient(#a2c9e1, #7ab6dc);
  background-image: linear-gradient(#a2c9e1, #7ab6dc);
  box-shadow: 0 1px 6px #476274 inset;
  border-color: #79a8c7;
}

/* Size */
input.ch-btn::-moz-focus-inner,
input.ch-btn-large::-moz-focus-inner,
input.ch-btn-small::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/* Large */
.ch-btn-large,
.ch-btn-large:focus {
  font-size: 20px;
  text-align: center;
}

/* Disabled */
[class*="ch-btn"].ch-btn-disabled,
.ch-btn-disabled:hover,
.ch-btn-disabled:active,
.ch-btn-disabled:focus,
[class*="ch-btn"][disabled],
[class*="ch-btn"][disabled]:hover,
[class*="ch-btn"][disabled]:active,
[class*="ch-btn"][disabled]:focus {
  background-color: #f9f9f9;
  background-image: -webkit-linear-gradient(#f9f9f9, #dedede);
  background-image: linear-gradient(#f9f9f9, #dedede);
  border: 1px solid #cdcdcd !important;
  /* important for links */
  box-shadow: 0 1px 1px #fff inset;
  color: #999 !important;
  /* important for links */
  cursor: not-allowed;
  opacity: 0.5;
  text-shadow: none;
  -ms-touch-action: none;
  pointer-events: none;
}

/* Action button */
.ch-btn-action,
.ch-btn-action:focus,
.ch-btn-action:visited {
  background-color: #dedede;
  background-image: -webkit-linear-gradient(#dedede, #cdcdcd);
  background-image: linear-gradient(#dedede, #cdcdcd);
  border: 1px solid #bbb;
  box-shadow: 0 1px 0 #fff;
  color: #666;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.5);
}

/* Colors on Hover */
.ch-btn-action:hover {
  background-color: #fff;
  background-image: -webkit-linear-gradient(#fff, #dedede);
  background-image: linear-gradient(#fff, #dedede);
}

.ch-btn-action:active {
  background-color: #a9a9a9;
  background-image: -webkit-linear-gradient(#a9a9a9, #cdcdcd);
  background-image: linear-gradient(#a9a9a9, #cdcdcd);
  border-color: #999;
  box-shadow: 0 1px 6px rgba(255, 255, 255, 0.5);
}

/* Default h: ~38px */
.ch-btn,
.ch-btn:focus {
  line-height: 1.4em;
  outline: 0;
  padding: 7px 14px;
}

/* Large h: ~44px */
.ch-btn-large,
.ch-btn-large:focus {
  width: 100%;
  line-height: 1.5em;
}

a.ch-btn-large,
a.ch-btn-large:focus {
  box-sizing: border-box;
}

/* Small h: ~26px */
.ch-btn-small,
.ch-btn-small:focus {
  font-size: 13px;
  line-height: 1.4em;
  padding: 5px 10px;
}

/**
 * Popover
 */
.ch-popover {
  box-shadow: 2px 2px 2px rgba(100, 100, 100, 0.2);
  z-index: 1000;
}

.ch-underlay + .ch-popover {
  z-index: 1050;
}

.ch-popover-wrapper {
  display: inline-block;
  position: relative;
}

.ch-shownby-pointertap {
  cursor: pointer;
}

.ch-shownby-pointerenter {
  cursor: default;
}

/**
 * Close button: must generate a space
 */
.ch-popover .ch-close + div {
  margin: 0 24px 0 0;
}

/**
 * Cone
 */
.ch-cone .ch-popover-content:before,
.ch-cone .ch-popover-content:after {
  border: outset transparent;
  /* border transparent bug on FF: http://stackoverflow.com/a/6225439 */
  content: '';
  height: 0;
  width: 0;
  position: absolute;
  -ms-touch-action: none;
  pointer-events: none;
  display: block;
}

/*
* Triangle with border.
*/
.ch-cone .ch-popover-content:before {
  border-width: 10px;
}

.ch-cone .ch-popover-content:after {
  border-width: 9px;
}

/**
 * Bottom side
 * ltlb, ctcb, rtrb
 */
.ch-cone[data-side=bottom] .ch-popover-content:before,
.ch-cone[data-side=bottom] .ch-popover-content:after {
  bottom: 100%;
  border-bottom-style: solid;
}

.ch-cone[data-side=bottom] .ch-popover-content:before {
  border-bottom-color: #dedede;
}

.ch-cone[data-side=bottom] .ch-popover-content:after {
  border-bottom-color: #fff;
}

/**
 * Top side
 * lblt, cbct, rbrt
 */
.ch-cone[data-side=top] .ch-popover-content:before,
